import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { BookOpen, Youtube, BookmarkCheck, GraduationCap, ChevronDown, X } from 'lucide-react';

interface CurriculumCardProps {
  termData: Term;
}

export const CurriculumCard = ({ termData }: CurriculumCardProps) => {
  const [isExpanded, setIsExpanded] = useState(false);

  const getIcon = (type: Resource['type']) => {
    switch (type) {
      case 'book':
        return <BookOpen className="w-5 h-5" />;
      case 'youtube':
        return <Youtube className="w-5 h-5" />;
      case 'challenge':
        return <BookmarkCheck className="w-5 h-5" />;
      case 'course':
        return <GraduationCap className="w-5 h-5" />;
      default:
        return null;
    }
  };

  const cardVariants = {
    collapsed: {
      width: '100%',
      height: '300px',
      position: 'relative' as const,
    },
    expanded: {
      width: '100vw',
      height: '100vh',
      position: 'fixed' as const,
      top: 0,
      left: 0,
      zIndex: 50,
    },
  };

  const contentVariants = {
    collapsed: {
      opacity: 1,
    },
    expanded: {
      opacity: 1,
    },
  };

  return (
    <motion.div
      layout
      variants={cardVariants}
      initial="collapsed"
      animate={isExpanded ? 'expanded' : 'collapsed'}
      transition={{ duration: 0.3, ease: 'easeInOut' }}
      className="bg-white rounded-lg shadow-lg overflow-hidden cursor-pointer"
      onClick={() => !isExpanded && setIsExpanded(true)}
    >
      <motion.div
        layout
        variants={contentVariants}
        className="h-full w-full"
      >
        {isExpanded ? (
          <div className="p-6 h-full overflow-y-auto">
            <div className="flex justify-between items-center mb-6">
              <h1 className="text-3xl font-bold">Term {termData.term}</h1>
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  setIsExpanded(false);
                }}
                className="p-2 hover:bg-gray-100 rounded-full transition-colors"
              >
                <X className="w-6 h-6" />
              </button>
            </div>
            
            <div className="space-y-8">
              {termData.units.map((unit, index) => (
                <div key={index} className="border-b pb-6">
                  <h2 className="text-xl font-semibold mb-4">{unit.title}</h2>
                  <div className="space-y-4">
                    {unit.resources.map((resource, rIndex) => (
                      <div key={rIndex} className="pl-4">
                        <div className="flex items-center gap-2 text-gray-700">
                          {getIcon(resource.type)}
                          <span className="capitalize font-medium">{resource.type}</span>
                        </div>
                        
                        {resource.title && (
                          <a
                            href={resource.url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="block ml-7 text-blue-600 hover:underline"
                          >
                            {resource.title}
                          </a>
                        )}
                        
                        {resource.videos && (
                          <div className="ml-7 space-y-2 mt-2">
                            {resource.videos.map((video, vIndex) => (
                              <a
                                key={vIndex}
                                href={video.url}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="block text-blue-600 hover:underline"
                              >
                                {video.title}
                              </a>
                            ))}
                          </div>
                        )}
                        
                        {resource.tasks && (
                          <ul className="ml-7 list-disc pl-5 mt-2 space-y-1">
                            {resource.tasks.map((task, tIndex) => (
                              <li key={tIndex}>{task}</li>
                            ))}
                          </ul>
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <div className="p-6 h-full">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-2xl font-bold">Term {termData.term}</h2>
              <ChevronDown className="w-5 h-5 text-gray-500" />
            </div>
            <div className="space-y-2">
              {termData.units.map((unit, index) => (
                <div key={index} className="text-gray-600">
                  â€¢ {unit.title}
                </div>
              ))}
            </div>
          </div>
        )}
      </motion.div>
    </motion.div>
  );
};